import React from 'react';
import AsyncSelect from 'react-select/async';

import isEmpty from '../../validation/isEmpty';

import { Modal, Button, Form, Col, Card, Table, ButtonGroup } from 'react-bootstrap';
import { getItems } from '../../api/inventory/itemApi';
import ErrorBody from '../../App/components/Reusables/ErrorBody';
import { getPurchaseItems } from '../../api/inventory/purchaseItem';
import IsEmpty from '../../App/components/Reusables/IsEmpty';
import { getPlacement, updatePlacement } from '../../api/misc';
import { validatedPlacementCreation } from '../../validation/placement';
import { withRouter } from 'react-router-dom';
import Jhilikka from '../../App/components/Reusables/Jhilikka';

const CreatePlacementModal = React.memo((props) => {

    const [placementData, setPlacementData] = React.useState({
        quantity: '',
    });
    const [errors, setErrors] = React.useState({});

    const { selectedPlacement } = props




    const resetPlacementData = () => {
        setPlacementData({
            quantity: '',
        })
    }

    React.useEffect(() => {
        if (props.selectedPlacement) {
            setPlacementData({ ...placementData, quantity: props.selectedPlacement.stock })
        }
    }, [props.selectedPlacement])


    const _placementDataChange = (e) => {
        setPlacementData({
            ...placementData,
            [e.target.name]: e.target.type === 'number' ? parseFloat(e.target.value) : e.target.value
        })
        setErrors({ ...errors, [e.target.name]: '' })
    }

    const _update = () => {
        if (isEmpty(placementData.quantity)) {
            setErrors({ ...errors, quantity: 'Quantity field cannot be empty!' })
            return;
        }
        console.log('no errors');
        const requestJson = {
            "action": "update",
            "placement_id": parseInt(props.selectedPlacement.id),
            "quantity": parseInt(placementData.quantity),
            "place_id": props.placeId
        }


        updatePlacement(requestJson)
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Placement updated successfully!', background: '#dffff0' })
                    await resetPlacementData()
                    await props.history.push('/place/' + props.match.params.id);
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: "#ffe5d6" });
                }
            }).catch((err) => {
                console.log(err)
            });
    }

    console.log('---- tip: show available qty as reference to quantity update ----------')


    return (
        <>
            <Modal.Header closeButton style={{ margin: 'auto 5px' }}>
                <Modal.Title style={{ paddingLeft: '15px', color: '#3d4075', fontSize: '1.4em', fontWeight: 'bold' }}>Update Placement</Modal.Title>
            </Modal.Header>

            <Modal.Body
                style={{ paddingLeft: '30px' }}
            >
                <Col md={6}>
                    <div className='mb-1 border-bottom'><b>Item Name</b></div>
                    <div className='mt-1 mb-3'>
                        {selectedPlacement.item_name}
                    </div>
                </Col>
                <Col md={6}>
                    <div className='mb-1 border-bottom'><b>Purchase Order UUID</b></div>
                    <div className='mt-1 mb-3'>
                        {selectedPlacement.purchase_order_uuid}
                    </div>
                </Col>
                <Col md={6}>
                    <div className='mb-1 border-bottom'><b>Sales Price</b></div>
                    <div className='mt-1 mb-3'>
                        {selectedPlacement.sales_price}
                    </div>
                </Col>


                <Col md={6} >
                    <Form.Group  >

                        <Form.Label>Quantity*</Form.Label>
                        <Form.Control
                            name="quantity"
                            value={placementData.quantity || ""}
                            onChange={_placementDataChange}
                            type="number"
                            placeholder="Quantity"
                        />
                        <ErrorBody>{errors.quantity}</ErrorBody>
                    </Form.Group>
                </Col>

            </Modal.Body>


            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', padding: '10px' }}>
                <Button variant="secondary" onClick={props.onCancel}>
                    Cancel
                </Button>
                <Button variant="primary" onClick={_update}>
                    Update
                </Button>
            </div>

        </>
    )

}, (prevProps, nextProps, x) => {
    // console.log(prevProps, nextProps, x);
})

export default withRouter(CreatePlacementModal);