import React from 'react';
import { connect } from 'react-redux';
import { components } from 'react-select';
import PropTypes from 'prop-types'


const CustomMenuList = props => {

    let activeFetchMoreFor;

    //findout which fetch options is to be used
    for (let key in props.selectDropdown) {
        if (props.selectDropdown[key]) {
            activeFetchMoreFor = key;
            break;
        }
    }


    return (
        <components.MenuList {...props}>
            {props.children}


            {props.selectDropdown[activeFetchMoreFor] ?
                <div
                    style={{
                        padding: '8px 12px',
                        backgroundColor: '#f4f7fad9',
                    }}
                >
                    <i className="fa fa-spinner text-c-white f-15 fa-pulse mr-1"></i>
                    <b>Loading...</b>
                </div>
                : null
            }


        </components.MenuList>
    );
};

CustomMenuList.propTypes = {
    selectDropdown: PropTypes.object.isRequired,
}

const mapStateToProps = state => ({
    selectDropdown: state.selectDropdown
})

export default connect(mapStateToProps, {})(CustomMenuList)