import isEmpty from "./isEmpty"


const validatedPlacementCreation = (placementData) => {


    const errors = {}


    if (isEmpty(placementData.purchaseItemId)) {
        errors.purchaseItemId = 'Purchase Item should be chosen!'
    }
    if (isEmpty(placementData.quantity)) {
        errors.quantity = 'Quantity of item should be provide!'
    }


    //Will be valid if the errors are empty
    const isValid = isEmpty(errors);
    return { isValid, errors }
}

export { validatedPlacementCreation }