import React from 'react';
import AsyncSelect from 'react-select/async';

import isEmpty from '../../validation/isEmpty';

import { Modal, Button, Form, Col, Card, Table, ButtonGroup } from 'react-bootstrap';
import { getItems } from '../../api/inventory/itemApi';
import ErrorBody from '../../App/components/Reusables/ErrorBody';
import { getPurchaseItems } from '../../api/inventory/purchaseItem';
import IsEmpty from '../../App/components/Reusables/IsEmpty';
import { createPlacement } from '../../api/misc';
import { validatedPlacementCreation } from '../../validation/placement';
import { withRouter } from 'react-router-dom';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import DEMO from '../../globals/constant';
import store from '../../redux/store';
import { RESET_FETCH_MORE_OPTIONS, SET_FETCH_MORE_OPTIONS, SET_IS_MENU_OPEN } from '../../redux/actions/types';
import CustomSelectMenuList from '../../App/components/Reusables/CustomSelectMenuList';

const CreatePlacementModal = React.memo((props) => {

    const [submitting, setSubmitting] = React.useState(false);


    const [itemInputText, setItemInputText] = React.useState('');
    const [itemOptionsMaxLength, setItemOptionsMaxLength] = React.useState(0);
    const [defaultItemOptions, setDefaultItemOptions] = React.useState([]);


    const [purchaseItemsArr, setPurchaseItemsArr] = React.useState([])
    const [placementData, setPlacementData] = React.useState({
        itemId: null,
        itemName: null,
        purchaseItemId: null,
        quantity: '',
    });

    const getItems_ = async (signal) => {
        console.log('api call fired to get items')
        await getItems({
            "action": 'get',
            "filter": 'none',
            "start": defaultItemOptions.length,
            "end": defaultItemOptions.length + DEMO.INSTANCE_PER_LOAD
        }, signal)
            .then(res => {
                console.log('here is one', res);
                if (res.json.status) {
                    setDefaultItemOptions([
                        ...defaultItemOptions,
                        ...res.json.items.map((itm, index) => ({ label: `${itm.name}`, value: itm.id, price: itm.average_cost_price, weight: itm.weight || 0 }))
                    ]);
                    setItemOptionsMaxLength(res.json.count)
                    store.dispatch({
                        type: SET_FETCH_MORE_OPTIONS,
                        payload: ['fetchMoreOptionsItems', false]
                    })
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            })
            .catch(err => {
                console.log(err)
            })
    }

    const [bounds, setBounds] = React.useState({
        start: 0,
        end: 50,
    })
    const [errors, setErrors] = React.useState({});



    React.useEffect(() => {
        const controller = new AbortController();
        getItems_(controller.signal);
        resetPlacementData();
        return () => controller.abort();
    }, [])

    const getPurchaseItemsArr = async () => {
        await getPurchaseItems({ action: 'get', filter: 'item_on_default', item_id: parseInt(placementData.itemId), start: parseInt(bounds.start), end: parseInt(bounds.end) })
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    setPurchaseItemsArr([...res.json.purchase_items])
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });
    }



    const resetPlacementData = () => {
        setPlacementData({
            itemId: null,
            itemName: null,
            purchaseItemId: null,
            quantity: '',
        })
    }


    React.useEffect(() => {
        if (placementData.itemId) {
            getPurchaseItemsArr();
        }
    }, [placementData.itemId, bounds])


    const loadItemOptions = async (inputText) => {
        const requestJson = { action: 'get', filter: 'name', name: inputText, start: 0, end: 50 }
        return getItems(requestJson)
            .then((res) => {
                if (res.json.status) {
                    return res.json.items.map((itm, index) => ({ label: `${itm.name}`, value: itm.id }));
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            }).catch((err) => {
                console.log(err)
            });

    }


    const _itemInputChange = (itemInputText) => {
        setItemInputText(itemInputText);
    }

    const _scrollItemMenuToBottom = (e) => {
        e.preventDefault();
        if (itemOptionsMaxLength > defaultItemOptions.length && !itemInputText) {
            store.dispatch({
                type: SET_FETCH_MORE_OPTIONS,
                payload: ['fetchMoreOptionsItems', true]
            })
            getItems_();
        }
    }

    const _selectedItemChange = item => {
        setErrors({ ...errors, itemId: '' })
        setPlacementData({ ...placementData, itemId: item.value, itemName: item.label })
    };

    const _placementDataChange = (e) => {
        setPlacementData({
            ...placementData,
            [e.target.name]: e.target.type === 'number' ? parseFloat(e.target.value) : e.target.value
        })
        setErrors({ ...errors, [e.target.name]: '' })
    }

    const _submit = () => {
        const { isValid, errors } = validatedPlacementCreation(placementData);
        if (!isValid) {
            setErrors({ ...errors })
            return;
        }

        setSubmitting(true);
        const requestJson = {
            "action": "add",
            "place_id": parseInt(props.placeId),
            "purchase_item_id": parseInt(placementData.purchaseItemId),
            "quantity": parseInt(placementData.quantity),
        }
        createPlacement(requestJson)
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Item assigned successfully!', background: '#dffff0' })
                    await resetPlacementData()
                    await props.history.push('/place/' + props.match.params.id);
                } else {
                    setSubmitting(false);
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: "#ffe5d6" });
                }
            }).catch((err) => {
                setSubmitting(false)
                console.log(err)
            });
    }



    const PurchaseItemsList = purchaseItemsArr.map((pi, index) => {
        return (
            <tr key={index}
                style={{ cursor: 'pointer', backgroundColor: pi.id === placementData.purchaseItemId ? '#8ae0ff' : 'white', color: '#2f3133' }}
                onClick={() => {
                    setPlacementData({ ...placementData, purchaseItemId: pi.id })
                    setErrors({ ...errors, purchaseItemId: '' })
                }
                }
            >
                <th scope="row">{pi.id}</th>
                <td>{pi.vendor}</td>
                <td>{pi.purchase_order_id}</td>
                <td>{pi.stock}</td>
                <td>{pi.on_default}</td>
            </tr>
        )
    })

    const selectedRows = purchaseItemsArr.find((itm, ind) => itm.id === placementData.purchaseItemId);
    if (selectedRows) {
        console.log(selectedRows);
    }
    console.log(placementData);
    return (
        <>
            <Modal.Header closeButton style={{ margin: 'auto 5px' }}>
                <Modal.Title style={{ paddingLeft: '15px', color: '#3d4075', fontSize: '1.4em', fontWeight: 'bold' }}>Create Placement</Modal.Title>
            </Modal.Header>

            <Modal.Body
                style={{ paddingLeft: '30px' }}
            >
                <Col md={6} >

                    <Form.Group  >
                        <Form.Label>Item*</Form.Label>
                        <AsyncSelect
                            defaultOptions={defaultItemOptions}
                            value={placementData.itemId ? { label: placementData.itemName, value: placementData.itemId } : null}
                            onChange={_selectedItemChange}
                            loadOptions={loadItemOptions}
                            placeholder='Select...'
                            onInputChange={_itemInputChange}
                            components={{
                                MenuList: CustomSelectMenuList,
                            }}

                            onMenuScrollToBottom={_scrollItemMenuToBottom}
                            onMenuClose={() => {
                                store.dispatch({ type: RESET_FETCH_MORE_OPTIONS })
                                store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['item', false] })
                            }}
                            onMenuOpen={() => {
                                store.dispatch({ type: SET_IS_MENU_OPEN, payload: ['item', true] })
                            }}
                        />
                    </Form.Group>
                </Col>

                <Card.Body>
                    <Card.Title as="h5" style={{ marginTop: '5px' }} >Select purchase item to be placed</Card.Title>
                    <Table responsive hover>
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>Vendor</th>
                                <th>Order UUID</th>
                                <th>Stock</th>
                                <th>On Default</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {PurchaseItemsList || ''}
                        </tbody>
                    </Table>
                    {isEmpty(purchaseItemsArr) && !placementData.itemId ?
                        <><IsEmpty>Select item first!</IsEmpty><br /><br /> <br /></> :
                        (placementData.itemId && isEmpty(purchaseItemsArr) ?
                            <><IsEmpty>Data not available!</IsEmpty><br /><br /> <br /></> :
                            '')}
                    <ErrorBody>{errors.purchaseItemId}</ErrorBody>
                </Card.Body>
                <Card.Footer style={{ background: 'none', display: 'flex', justifyContent: 'center' }} >
                    <ButtonGroup aria-label="Second group">
                        <Button
                            onClick={() => setBounds({ ...bounds, start: bounds.start === 0 ? 0 : bounds.start - 50, end: bounds.end === 50 ? 50 : bounds.end - 50 })}
                        >
                            <i className="feather icon-chevron-left text-c-white f-15 m-0 " />
                        </Button>
                        <Button
                            onClick={() => setBounds({ ...bounds, start: bounds.start + 50, end: bounds.end + 50 })}
                        >
                            <i className="feather icon-chevron-right text-c-white f-15 m-0" />
                        </Button>
                    </ButtonGroup>
                </Card.Footer>

                <Col md={6} >
                    <Form.Group  >

                        <Form.Label>Quantity*</Form.Label>
                        <Form.Control
                            name="quantity"
                            value={placementData.quantity || ""}
                            onChange={_placementDataChange}
                            type="number"
                            placeholder="Quantity"
                        />
                        <ErrorBody>{errors.quantity}</ErrorBody>
                    </Form.Group>
                </Col>

            </Modal.Body>


            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'flex-end', padding: '10px' }}>
                <Button variant="secondary" onClick={props.onCancel}>
                    Cancel
                </Button>
                <Button disabled={submitting} variant="primary" onClick={_submit}>
                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}
                </Button>
            </div>

        </>
    )

}, (prevProps, nextProps, x) => {
    console.log(prevProps, nextProps, x);
})

export default withRouter(CreatePlacementModal);