import React from 'react';
import { Row, Col, Card, Table, Tabs, Tab, Badge, Modal } from 'react-bootstrap';

import Loader from '../../App/layout/Loader/index'



import DEMO from '../../globals/constant';
import DeleteModal from '../../App/components/Reusables/DeleteModal';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import isEmpty from '../../validation/isEmpty';
import IsEmpty from '../../App/components/Reusables/IsEmpty';
import ItemsTable from '../Items/ItemsTable';
import { getPlace, deletePlaces } from '../../api/inventory/placeApi';
import { getPlacements, deletePlacement } from '../../api/misc'
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import CreatePlacementModal from './CreatePlacementModal';
import UpdatePlacementModal from './UpdatePlacementModal';

let path = []
const createPath = async (parent_tree) => {
    path.push(parent_tree.details[0])
    if (!isEmpty(parent_tree.sub_categories)) {
        createPath(parent_tree.sub_categories)
    }
    return;
}

const ItemCategory = (props) => {


    const [loading, setLoading] = React.useState(true);
    const [placeData, setPlaceData] = React.useState(null);
    const [deletePlaceData, setDeletePlaceData] = React.useState({ showModal: false, place: null });
    const [deletePlacementData, setDeletePlacementData] = React.useState({ showModal: false, placementId: null });
    const [instanceCount, setInstanceCount] = React.useState(0)
    const [placementsArr, setPlacementsArr] = React.useState([]);
    const [createPlacementData, setCreatePlacementData] = React.useState({
        showModal: false,
        place_id: null,
        quantity: '',
    })
    const [updatePlacementData, setUpdatePlacementData] = React.useState({
        showModal: false,
        selectedPlacement: null,
    })

    React.useEffect(() => {
        const controller = new AbortController();
        (async () => {
            let ladder = 0;
            await getPlace({
                action: 'get',
                place_id: props.match.params.id
            }, controller.signal)
                .then(async (res) => {
                    console.log(res)
                    if (res.json.status) {
                        setPlaceData(res.json.place)
                        ladder++;
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err);
                });

            //Get items in this place 

            const queryObj = new URLSearchParams(props.location.search)
            await getPlacements({
                "action": 'get',
                "filter": 'place',
                "place": props.match.params.id,
                "start": parseInt(queryObj.get('start')) || 0,
                "end": (parseInt(queryObj.get('end')) || 25),

            }, controller.AbortController)
                .then((res) => {
                    console.log(res)
                    if (res.json.status) {
                        setPlacementsArr([...res.json.placements])
                        setInstanceCount(res.json.count)
                        ladder++;
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });

            if (ladder === 2) {
                setLoading(false)
            }
        })()
        return () => controller.abort()
    }, []);



    const _triggerDeletePlaceModal = (e) => {
        setDeletePlaceData({ showModal: true, placeId: props.match.params.id });
    }

    const _triggerDeletePlacementModal = (id) => {
        setDeletePlacementData({ showModal: true, placementId: id });
    }

    const _deletePlace = (toBeDeletedId) => {
        deletePlaces({ action: 'delete', places_id: [toBeDeletedId] })
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    setDeletePlaceData({ showModal: false, placeId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Place deleted successfully!', background: '#dffff0' })
                    props.history.push('/places')
                } else {
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                console.log(err);
            });
    }
    const _deletePlacement = (toBeDeletedId) => {
        deletePlacement({ action: 'delete', placement_id: toBeDeletedId })
            .then(async (res) => {
                console.log(res)
                if (res.json.status) {
                    setDeletePlacementData({ showModal: false, placementId: null })
                    Jhilikka.fire({ icon: 'success', title: 'Placement removed successfully!', background: '#dffff0' })
                    props.history.push('/place/' + props.match.params.id)
                } else {
                    Jhilikka.fire({ icon: 'success', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                console.log(err);
            });
    }


    const PlacementsList = placementsArr.map((plm, index) => {
        return (
            <tr key={index}>
                <th scope="row">{plm.id}</th>
                <td
                    onClick={() => {
                        props.history.push('/item/' + plm.item)
                    }}
                    style={{ cursor: 'pointer', color: '#55588b' }}
                >{plm.item_name}</td>
                <td

                    onClick={() => {
                        props.history.push('/purchase-order/' + plm.purchase_order_id)
                    }}
                    style={{ cursor: 'pointer', color: '#55588b' }}
                >{plm.purchase_order_uuid}</td>
                <td>Rs. {plm.sales_price}</td>
                <td>{plm.stock}</td>
                <td>{plm.is_active ? 'Yes' : 'No'}</td>
                <td>
                    <i
                        className="feather icon-delete text-c-red f-16 m-r-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => _triggerDeletePlacementModal(plm.id)}
                    />
                    <i
                        className="feather icon-edit text-c-green f-16 m-l-5"
                        style={{ cursor: 'pointer' }}
                        onClick={() => setUpdatePlacementData({ ...updatePlacementData, showModal: true, selectedPlacement: plm })}
                    />
                </td>
            </tr>
        )
    })


    if (loading) return <Loader />
    return (
        <>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10" style={{ lineHeight: '33px' }} >{placeData.name}</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`/place/update/` + placeData.id)}
                    > <i className="feather icon-edit text-c-white f-15 m-r-5" />Update</button>
                    <button
                        className="btn btn-danger btn-sm shadow-2 mb-4"
                        onClick={_triggerDeletePlaceModal}
                    > <i className="feather icon-trash-2 text-c-white f-15 m-r-5" />Delete</button>
                </div>
            </Row>
            <Row>
                <Col xl={12}>
                    <Card>
                        <Card.Body>
                            <h5 className='mb-4'>Place Details</h5>
                            <div style={{ position: 'relative', top: '-20px' }}>


                            </div>
                            <br />
                            <div className="row d-flex align-items-center">

                                <Col md={2}></Col>
                                <Col md={8}>

                                    <div className='mb-1 border-bottom'><b>Place Name</b></div>
                                    <div className='mt-1 mb-3'>
                                        {placeData.name} {" "}
                                        {placeData.is_active ? (<Badge variant="info">Active</Badge>) : (<Badge variant="secondary">Inactive</Badge>)}
                                    </div>
                                    <div className='mb-1 border-bottom'><b>No of Items</b></div>
                                    <div className='mt-1 mb-3'>
                                        {placeData.count_assignment}
                                    </div>


                                </Col>
                                <Col md={2}></Col>

                            </div>
                            <br />


                            <Card.Body>
                                <Card.Header style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                                    <Card.Title as="h5" style={{ marginTop: '5px' }} >Items in {placeData.name}</Card.Title>
                                    <button
                                        className="btn btn-primary btn-sm shadow-2, m-0"
                                        onClick={() => setCreatePlacementData({ ...createPlacementData, showModal: true })}
                                    > Place new item</button>
                                </Card.Header>
                                <Table striped responsive>
                                    <thead>
                                        <tr>
                                            <th>#</th>
                                            <th>Item Name</th>
                                            <th>Purchase Order</th>
                                            <th>Sales Price</th>
                                            <th>Stock</th>
                                            <th>Enabled</th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {PlacementsList || ''}
                                    </tbody>
                                </Table>
                                {isEmpty(placementsArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /><br /></> : ''}

                                {loading ? <Spinner /> : null}
                                <Paging instanceCount={instanceCount} />


                            </Card.Body>
                        </Card.Body>
                    </Card>
                </Col>

                <Modal
                    size="lg"
                    aria-labelledby="contained-modal-title-vcenter"
                    centered
                    style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
                    show={createPlacementData.showModal}
                    onHide={() => setCreatePlacementData({ ...createPlacementData, showModal: false, })}
                >
                    <CreatePlacementModal
                        placeId={placeData.id}
                        onCancel={() => setCreatePlacementData({ ...createPlacementData, showModal: false, })}
                    />
                </Modal>


                <Modal
                    size="lg"
                    aria-labelledby="contained-modal-title-vcenter"
                    style={{ border: 'none', color: '#3d4075', boxShadow: '0 0 10px red' }}
                    show={updatePlacementData.showModal}
                    onHide={() => setUpdatePlacementData({ ...updatePlacementData, showModal: false, })}
                >
                    <UpdatePlacementModal
                        placeId={placeData.id}
                        selectedPlacement={updatePlacementData.selectedPlacement}
                        onCancel={() => setUpdatePlacementData({ ...updatePlacementData, showModal: false, })}
                    />
                </Modal>

                <DeleteModal
                    show={deletePlaceData.showModal}
                    onCancel={() => setDeletePlaceData({ ...deletePlaceData, showModal: false })}
                    onDelete={() => _deletePlace(deletePlaceData.placeId)}
                />

                <DeleteModal
                    message={`The item will be removed from ${placeData.name} and will added to Default place.`}
                    show={deletePlacementData.showModal}
                    onCancel={() => setDeletePlacementData({ ...deletePlacementData, showModal: false })}
                    onDelete={() => _deletePlacement(deletePlacementData.placementId)}
                />

            </Row>
        </>
    );
}

export default ItemCategory;
